.calculator .col-1,
.calculator .col-2,
.calculator .col-3,
.calculator .col-4,
.calculator .col-5,
.calculator .col-6,
.calculator .col-7,
.calculator .col-8,
.calculator .col-9,
.calculator .col-10,
.calculator .col-11,
.calculator .col-12
{
    padding: 0;
}

.calculator button {
    display: block;
    background: none;
    border: none;
    padding: 0;
    font-family: inherit;
    user-select: none;
    cursor: pointer;
    outline: none;

    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.calculator button:active {
    box-shadow: inset 0px 0px 80px 0px rgba(0,0,0,0.25);
}

.calculator #wrapper {
    height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;
}

.calculator #app {
    width: 320px;
    height: 520px;
    position: relative;
}

.calculator {
    width: 100%;
    height: 100%;
    background: black;

    display: flex;
    flex-direction: column;
}

.calculator #wrapper .calculator {
    box-shadow: 0px 0px 20px 0px #aaa;
}

.calculator .calculator-display {
    color: white;
    background: #1c191c;
    line-height: 130px;
    font-size: 6em;
    width: 100%;

    flex: 1;
}

.calculator input {
    border: none;
    position: relative;
    font-size: 100%;
    width: 100%;
    background-color: transparent;
    color: transparent;
    direction: rtl;
    z-index: 10;
}

.calculator input:focus {
    outline: none;
}

.calculator .auto-scaling-text {
    display: inline-block;
}

.calculator .calculator-display .auto-scaling-text {
    margin: 0 20px;
    padding: 0 30px;
    position: absolute;
    right: 0;
    transform-origin: right;
}

.calculator .calculator-keypad {
    height: 400px;

    display: flex;
}

.calculator .input-keys {
    width: 240px;
}

.calculator .function-keys {
    display: flex;
}

.calculator .digit-keys {
    background: #e0e0e7;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap-reverse;
}

.calculator .calculator-key {
    width: 80px;
    height: 80px;
    border-top: 1px solid #777;
    border-right: 1px solid #666;
    text-align: center;
    line-height: 80px;
}
.calculator .function-keys .calculator-key {
    font-size: 2em;
}
.calculator .function-keys .key-multiply {
    line-height: 50px;
}
.calculator .digit-keys .calculator-key {
    font-size: 2.25em;
}
.calculator .digit-keys .key-0 {
    width: 160px;
    text-align: left;
    padding-left: 32px;
}
.calculator .digit-keys .key-dot {
    padding-top: 1em;
    font-size: 0.75em;
}
.calculator .operator-keys .calculator-key {
    width: 100%;
    color: white;
    border-right: 0;
    font-size: 3em;
}

.calculator .function-keys {
    background: linear-gradient(to bottom, rgba(202,202,204,1) 0%, rgba(196,194,204,1) 100%);
}
.calculator .operator-keys {
    background:  linear-gradient(to bottom, rgba(252,156,23,1) 0%, rgba(247,126,27,1) 100%);
}